import React, { useState } from 'react';
import { Menu, Layout } from 'antd';
import {
  HomeOutlined,
  BookOutlined,
  ExclamationCircleOutlined,
  BarChartOutlined,
  FileTextOutlined,
  EditOutlined,
  FileSearchOutlined
} from '@ant-design/icons';

const { Header } = Layout;

const NavigationHeader = ({ onNavigate, currentPage = 'home' }) => {
  const [selectedKey, setSelectedKey] = useState(currentPage);

  const handleMenuClick = (e) => {
    setSelectedKey(e.key);
    if (onNavigate) {
      onNavigate(e.key);
    }
  };

  const menuItems = [
    {
      key: 'home',
      icon: <HomeOutlined />,
      label: '首页',
    },
    // {
    //   key: 'knowledge',
    //   icon: <BookOutlined />,
    //   label: '知识体系',
    // },
    // {
    //   key: 'mistakes',
    //   icon: <ExclamationCircleOutlined />,
    //   label: '错题总结',
    // },
    // {
    //   key: 'progress',
    //   icon: <BarChartOutlined />,
    //   label: '学习进步图',
    // },
    // {
    //   key: 'testgen',
    //   icon: <FileTextOutlined />,
    //   label: '试卷生成',
    // },
    {
      key: 'chinese',
      icon: <EditOutlined />,
      label: '语文',
    },
    {
      key: 'transcript',
      icon: <FileSearchOutlined />,
      label: '学生中心',
    },
  ];

  return (
    <Header style={{ 
      position: 'fixed',
      top: 0,
      left: 0,
      right: 0,
      zIndex: 1000,
      padding: 0, 
      background: '#fff', 
      boxShadow: '0 2px 8px rgba(0,0,0,0.1)',
      borderBottom: '1px solid #f0f0f0'
    }}>
      <div style={{ 
        display: 'flex', 
        alignItems: 'center', 
        height: '100%',
        padding: '0 24px' 
      }}>
        <div style={{ 
          fontSize: '18px', 
          fontWeight: 'bold', 
          color: '#1890ff',
          marginRight: '32px'
        }}>
          CC Island
        </div>
        <Menu
          mode="horizontal"
          selectedKeys={[selectedKey]}
          onClick={handleMenuClick}
          items={menuItems}
          style={{ 
            flex: 1, 
            border: 'none',
            fontSize: '14px'
          }}
        />
      </div>
    </Header>
  );
};

export default NavigationHeader;
