import React, { useState } from 'react';
import { data } from '../../data/knowledge-base/chinese/pinyin.js';
import './test-shengmuyunmu.css';

const TestZhengtirendu = () => {
  const [selectedCategory, setSelectedCategory] = useState(null);
  const [selectedItem, setSelectedItem] = useState(null);

  const handleCategoryClick = (category) => {
    setSelectedCategory(category);
    setSelectedItem(null);
  };

  const handleItemClick = (item) => {
    setSelectedItem(item);
  };

  return (
    <div className="pinyin-container">
      
      <div className="category-list">
        {data
          .filter(category => category.title === '整体认读音节')
          .map((category, index) => (
            <div key={index} className="category-item">
              <h2>{category.title}</h2>
              <div className="item-grid">
                {category.data.map((item, itemIndex) => (
                  <div
                    key={itemIndex}
                    className={`item ${selectedItem === item ? 'selected' : ''}`}
                    onClick={(e) => {
                      e.stopPropagation();
                      handleItemClick(item);
                    }}
                  >
                    <div className="pinyin">{item.d1}</div>
                    <div className="example">{item.d2}</div>
                    {item.d3 && <div className="description">{item.d3}</div>}
                  </div>
                ))}
              </div>
            </div>
          ))}
      </div>

      {selectedItem && (
        <div className="detail-panel">
          <h3>详细信息</h3>
          <div className="detail-content">
            <p><strong>拼音：</strong>{selectedItem.d1}</p>
            <p><strong>示例：</strong>{selectedItem.d2}</p>
            {selectedItem.d3 && <p><strong>类型：</strong>{selectedItem.d3}</p>}
          </div>
        </div>
      )}
    </div>
  );
};

export default TestZhengtirendu;
