import React, { useState } from 'react';
import { Layout, Menu } from 'antd';
import TestHanzi from './test-hanzi';
import TestBushou from './test-bushou';
import TestShengmuYunmu from './test-shengmuyunmu';
import TestZhengtirendu from './test-zhengtirendu';

const { Sider, Content } = Layout;

const ChinesePage = () => {
  const [selectedKey, setSelectedKey] = useState('hanzi');

  const menuItems = [
    {
      key: 'shengmu',
      label: '声母韵母',
    },
    {
      key: 'zhengti',
      label: '整体认读音节',
    },
    {
      key: 'bushou',
      label: '汉字部首',
    },
    {
      key: 'hanzi',
      label: '汉字认读',
    },
    {
      key: 'chengyu',
      label: '简单成语',
    },
    {
      key: 'gushi',
      label: '简单古诗词',
    },
  ];

  const renderContent = () => {
    switch (selectedKey) {
      case 'hanzi':
        return <TestHanzi />;
      case 'bushou':
        return <TestBushou />;
      case 'shengmu':
        return <TestShengmuYunmu />;
      case 'zhengti':
        return <TestZhengtirendu />;
    default:
        return <div>功能开发中...</div>;
    }
  };

  return (
    <Layout style={{ minHeight: '100vh' }}>
      <Sider width={200} theme="light">
        <Menu
          mode="inline"
          selectedKeys={[selectedKey]}
          style={{ height: '100%', borderRight: 0 }}
          items={menuItems}
          onClick={({ key }) => setSelectedKey(key)}
        />
      </Sider>
      <Layout>
        <Content style={{ padding: '24px', minHeight: 280 }}>
          {renderContent()}
        </Content>
      </Layout>
    </Layout>
  );
};

export default ChinesePage;
