import React, { useState, useEffect } from 'react';
import Transcript from './transcript';
import './index.css';

const UserCenter = () => {
  const [selectedStudent, setSelectedStudent] = useState(null);
  const [selectedSubject, setSelectedSubject] = useState(null);
  const [students, setStudents] = useState([]);
  const [subjects] = useState(['汉字']); // 固定的科目列表

  useEffect(() => {
    // 获取学生目录下的所有文件夹作为学生列表
    const loadStudents = async () => {
      try {
        const context = require.context('../../data/user-data', true, /\.json$/);
        const studentFiles = context.keys();
        
        // 提取不重复的学生ID列表
        const studentIds = [...new Set(studentFiles.map(filePath => filePath.split('/')[1]))];
        setStudents(studentIds);
        
        // 如果有学生，默认选中第一个
        if (studentIds.length > 0) {
          setSelectedStudent(studentIds[0]);
          setSelectedSubject('汉字'); // 默认选中汉字科目
        }
      } catch (error) {
        console.error('加载学生列表失败:', error);
      }
    };

    loadStudents();
  }, []);

  return (
    <div className="user-center-container">
      {/* 左侧学生列表 */}
      <div className="student-list-column">
        <h2>学生列表</h2>
        <div className="student-list">
          {students.map(studentId => (
            <div
              key={studentId}
              className={`student-item ${selectedStudent === studentId ? 'selected' : ''}`}
              onClick={() => setSelectedStudent(studentId)}
            >
              {studentId}
            </div>
          ))}
        </div>
      </div>

      {/* 中间科目列表 */}
      <div className="subject-list-column">
        <h2>学习科目</h2>
        <div className="subject-list">
          {subjects.map(subject => (
            <div
              key={subject}
              className={`subject-item ${selectedSubject === subject ? 'selected' : ''}`}
              onClick={() => setSelectedSubject(subject)}
            >
              {subject}
            </div>
          ))}
        </div>
      </div>

      {/* 右侧成绩单详情 */}
      <div className="transcript-column">
        {selectedStudent && selectedSubject && (
          <Transcript studentId={selectedStudent} subject={selectedSubject} />
        )}
      </div>
    </div>
  );
};

export default UserCenter;
