import React from 'react';
import { data } from '../../data/knowledge-base/chinese/pinyin.js';
import './test-shengmuyunmu.css';

const TestShengmuYunmu = () => {
  // 从数据中获取声母和韵母
  const shengmu = data.find(item => item.title === '声母');
  const danYunmu = data.find(item => item.title === '单韵母');
  const fuYunmu = data.find(item => item.title === '复韵母');
  const qianBiYunmu = data.find(item => item.title === '前鼻韵母');
  const houBiYunmu = data.find(item => item.title === '后鼻韵母');

  const renderPinyinCard = (item) => (
    <div className="pinyin-card" key={item.d1}>
      <div className="pinyin">{item.d1}</div>
      <div className="example">{item.d2}</div>
      {item.d3 && <div className="category">{item.d3}</div>}
    </div>
  );

  const renderSection = (title, items) => (
    <div className="section">
      <h2>{title}</h2>
      <div className="cards-container">
        {items.data.map(renderPinyinCard)}
      </div>
    </div>
  );

  return (
    <div className="test-shengmu-yunmu">
      
      {shengmu && renderSection('声母', shengmu)}
      
      <div className="yunmu-container">
        {danYunmu && renderSection('单韵母', danYunmu)}
        {fuYunmu && renderSection('复韵母', fuYunmu)}
        {qianBiYunmu && renderSection('前鼻韵母', qianBiYunmu)}
        {houBiYunmu && renderSection('后鼻韵母', houBiYunmu)}
      </div>
    </div>
  );
};

export default TestShengmuYunmu;
